import React from "react";
import PropTypes from "prop-types";
import { goNext, goPrev } from "../../redux/query";
import { getBundleURL, parseQueryString } from "../../lib";
import DialogFooter from "../DialogFooter";
import "./Footer.less";

export default class Footer extends React.Component {
  static propTypes = {
    bundle: PropTypes.object,
    query: PropTypes.object.isRequired,
    dispatch: PropTypes.func.isRequired,
    selection: PropTypes.object.isRequired,
    canShowSelected: PropTypes.bool,
  };

  render() {
    let msg = this.props.query.error ? " Error! " : " Loading... ";
    let bundle = this.props.bundle;
    let hasPrev = bundle && getBundleURL(bundle, "previous");
    let hasNext = bundle && getBundleURL(bundle, "next");
    let url;

    if (hasNext) {
      url = getBundleURL(bundle, "next");
      if (url) {
        url = parseQueryString(url);
      }
    } else if (hasPrev) {
      url = getBundleURL(bundle, "previous");
      if (url) {
        url = parseQueryString(url);
      }
    }

    if (bundle && !this.props.query.error) {
      if (this.props.query.params._id) {
        msg = "Showing the selected patients only";
      } else {
        let len = bundle && bundle.entry ? bundle.entry.length : 0;
        if (len) {
          let pageOffset = this.props.query.page
            ? +url._count * (this.props.query.page - 1)
            : null;
          let startRec = +(this.props.query.offset || pageOffset || 0) + 1;
          let endRec = startRec + len - 1;

          msg = ` patient ${startRec} to ${endRec} `;

          if ("total" in bundle) {
            msg += ` of ${bundle.total} `;
          }
        } else {
          msg = ` No Records! `;
        }
      }
    }

    return (
      <div className="app-footer">
        <div className="container-fluid" style={{ width: "100%" }}>
          <div className="row">
            <div className="col-xs-3 col-sm-4 text-right">
              <a
                href="#prev"
                onClick={(e) => {
                  e.preventDefault();
                  this.props.dispatch(goPrev());
                }}
                disabled={!hasPrev}
              >
                <i className="fa fa-arrow-left" /> Prev
              </a>
            </div>
            <div className="col-xs-6 col-sm-4 text-center">{msg}</div>
            <div className="col-xs-3 col-sm-4 text-left">
              <a
                href="#next"
                onClick={(e) => {
                  e.preventDefault();
                  this.props.dispatch(goNext());
                }}
                disabled={!hasNext}
              >
                Next <i className="fa fa-arrow-right" />
              </a>
            </div>
          </div>
          <DialogFooter canShowSelected={this.props.canShowSelected} />
        </div>
      </div>
    );
  }
}



